.main-nav__switch {
  display: block;
  position: absolute;
  top: 27px;
  right: calc(50% - 140px);
  margin: 0;
  font-size: 0;
  border: none;
  cursor: pointer;
}
@media (min-width: 660px) {
  .main-nav__switch {
    top: 43px;
    right: calc(50% - 310px);
  }
}
