  .main-nav__switch {
    display: none;
  }
}
.main-nav__switch--closed {
  width: 50px;
  height: 24px;
  background-image: url("../img/icon-menu-burger.svg");
  background-color: transparent;
  background-repeat: no-repeat;
}
.main-nav__switch--opened {
  width: 25px;
  height: 25px;
  background-image: url("../img/icon-menu-cross.svg");
  background-color: transparent;
  background-repeat: no-repeat;
}
.main-nav__switch--closed:active ~ .site-list {
  display: block;
}
.main-nav__switch--opened:active ~ .main-nav__switch--closed {
  display: none;
}
.main-nav__list {
  width: 320px;
  margin: 0 auto;
}
@media (min-width: 660px) {
  .main-nav__list {
    width: 660px;
  }
}
@media (min-width: 1200px) {
  .main-nav__list {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    min-width: 620px;
    line-height: 18px;
    font-weight: 400;
    text-transform: none;
    text-align: right;
    list-style: none;
    padding: 0;
    margin: 0;
    margin-right: calc(50% - 480px);
    border: none;
    background-color: transparent;
  }
}
.main-nav__switch--closed:active ~ .site-list {
  display: block;
}
.main-nav__switch--opened:active ~ .site-list {
  display: none;
}
.main-nav__switch--opened:active ~ .main-nav {
  background-color: green;
}
.main-nav__switch--opened:active {
  right: calc(50% - 140px);
  background-image: url("../img/icon-menu-burger.svg");
  background-color: transparent;
  background-repeat: no-repeat;
  width: 50px;
  height: 24px;
}
